@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.page-header {
  background-image: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  margin-bottom: 120px;
}
@media (max-width: 1023px) {
  .page-header {
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .page-header {
    margin-bottom: 64px;
  }
}

.page-header__wrapper {
  width: 100%;
  max-width: 1366px;
  min-height: 658px;
  margin: 0 auto;
  padding: 30px 94px;
}
@media (max-width: 1023px) {
  .page-header__wrapper {
    padding: 30px 45px;
    min-height: 552px;
  }
}
@media (max-width: 767px) {
  .page-header__wrapper {
    padding: 15px;
    min-height: 491px;
  }
}

.page-header__cap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .page-header__cap {
    margin-bottom: 7px;
  }
}

.page-header__tel {
  margin-left: auto;
  margin-bottom: auto;
  padding: 0 10px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  z-index: 1;
}
.page-header__tel:hover {
  text-decoration: underline;
}
.page-header__tel:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .page-header__tel {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
    padding: 0;
    padding-top: 5px;
  }
}
@media (max-width: 767px) {
  .page-header__tel {
    font-size: 20px;
    line-height: 24px;
    padding-top: 0;
  }
}

.page-header__conteiner {
  display: flex;
}

.page-header__info {
  width: 40%;
  padding: 10px;
  padding-top: 70px;
  padding-right: 65px;
}
@media (max-width: 1023px) {
  .page-header__info {
    width: 78%;
    padding: 0;
    padding-top: 50px;
    padding-right: 30px;
  }
}
@media (max-width: 767px) {
  .page-header__info {
    width: 100%;
    padding-right: 0;
    padding-top: 56px;
  }
}

.page-header h1 {
  width: 66%;
  padding: 0;
  margin: 0;
  margin-left: auto;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 94px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffffff;
  z-index: 1;
}
.page-header h1 br {
  display: none;
}
@media (max-width: 1023px) {
  .page-header h1 {
    font-size: 72px;
    line-height: 86px;
    letter-spacing: 0.05em;
    text-transform: unset;
  }
}
@media (max-width: 767px) {
  .page-header h1 br {
    display: block;
  }
}

.page-header__city {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  text-align: end;
  color: #ffffff;
  margin: 0;
  padding: 10px;
  padding-right: 50px;
  margin-bottom: 5px;
}
@media (max-width: 1023px) {
  .page-header__city {
    font-size: 34px;
    line-height: 41px;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .page-header__city {
    font-size: 28px;
    line-height: 34px;
    margin-bottom: 10px;
  }
}

.page-header__city-conteiner {
  display: flex;
  margin-bottom: 50px;
  align-items: flex-end;
}
@media (max-width: 1023px) {
  .page-header__city-conteiner {
    margin-bottom: 39px;
  }
}
@media (max-width: 767px) {
  .page-header__city-conteiner {
    position: relative;
    margin-bottom: 27px;
  }
}

.page-header__desk {
  padding: 20px 17px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .page-header__desk {
    padding: 4px 0;
    padding-right: 18px;
  }
}
@media (max-width: 767px) {
  .page-header__desk {
    padding: 0;
    margin: 0;
    margin-bottom: 28px;
    width: 39%;
    position: absolute;
  }
}

.page-header__button {
  display: block;
  padding: 19px;
  margin-left: auto;
  width: 59%;
  user-select: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  border: 4px solid #ffffff;
}
.page-header__button:hover {
  color: #102654;
  background-color: #ffffff;
}
.page-header__button:active {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .page-header__button {
    width: 57%;
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .page-header__button {
    width: 100%;
  }
}

.page-header img {
  display: block;
  margin-top: -30px;
  width: 100%;
  object-fit: cover;
}
@media (max-width: 1023px) {
  .page-header img {
    height: 100%;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .page-header img {
    display: none;
  }
}

.page-footer {
  background-color: #030a1a;
}
@media (max-width: 767px) {
  .page-footer {
    min-height: 260px;
  }
}

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1366px;
  padding: 0 93px;
  margin: 0 auto;
  min-height: 108px;
  align-items: center;
}
@media (max-width: 1023px) {
  .page-footer__wrapper {
    padding: 0 34px;
  }
}
@media (max-width: 767px) {
  .page-footer__wrapper {
    flex-direction: column;
    padding: 36px 10px;
  }
}

.footer-nav {
  width: 70%;
}
@media (max-width: 1023px) {
  .footer-nav {
    width: 80%;
  }
}
@media (max-width: 767px) {
  .footer-nav {
    width: 100%;
    margin-bottom: 20px;
  }
}

.footer-nav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .footer-nav__list {
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .footer-nav__list {
    display: grid;
    grid-template-columns: 1fr 9% 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}

.footer-nav__item--logo {
  margin-right: auto;
}
@media (max-width: 767px) {
  .footer-nav__item {
    margin: 3px;
    margin-bottom: 0;
  }
  .footer-nav__item--logo {
    margin-top: 10px;
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .footer-nav__item--services {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .footer-nav__item--tickets {
    grid-column: 1/2;
    grid-row: 3/4;
  }
  .footer-nav__item--stocks {
    grid-column: 3/4;
    grid-row: 1/2;
  }
  .footer-nav__item--coaches {
    grid-column: 3/4;
    grid-row: 2/3;
  }
  .footer-nav__item--schedule {
    grid-column: 3/4;
    grid-row: 3/4;
  }
}

.footer-nav__link {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  padding: 20px;
  cursor: pointer;
}
.footer-nav__link:active {
  opacity: 0.7;
}
