.video {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 62.25%;
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}
@media (max-width: 767px) {
  .video__media {
    top: 15px;
    height: unset;
  }
}

.video__button {
  position: absolute;
  width: 100px;
  height: 100px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  background-image: url("/img/svg/play-video.svg");
  border: none;
  cursor: pointer;
  z-index: 1;
  display: none;
}
@media (max-width: 1023px) {
  .video__button {
    width: 73px;
    height: 73px;
    background-image: url("/img/svg/play-video-mobile.svg");
  }
}

.video:hover .video__button {
  outline: 2px solid #ffffff;
  outline-offset: -2px;
  border-radius: 50%;
}

.video:active .video__button {
  opacity: 0.5;
}

.video--enabled .video__button {
  display: block;
}

.logo {
  display: block;
  padding: 10px;
}
@media (max-width: 1023px) {
  .logo {
    padding: 10px 0;
  }
}
@media (max-width: 767px) {
  .logo {
    padding: 0;
  }
}
.logo--footer {
  padding: 10px;
  padding-top: 25px;
}
@media (max-width: 1023px) {
  .logo--footer {
    padding: 10px;
    padding-top: 22px;
  }
}

@media (max-width: 1023px) {
  .logo__svg {
    padding: 0;
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .logo__svg {
    width: 68px;
    height: 24px;
  }
}
.logo__svg--footer {
  width: 88px;
  height: 32px;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

